@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-outfit);
  background-color: var(--bg1);
  color: white;
  min-height: 100vh;
}
@media (min-width: 64em) {
  body {
    display: flex;
  }
}

ul, li {
  list-style: none;
}

.flex {
  display: flex;
}

:root {
  --bg1: #10141E;
  --bg2: #161D2F;
  --red: #FC4747;
  --inactive-icon: #5A698F;
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--bg2);
  padding: 1.125rem 1rem;
}
@media (min-width: 40em) {
  .header {
    border-radius: 0.625rem;
    margin: 24px;
    padding: 1.25rem 1.5rem;
  }
}
@media (min-width: 64em) {
  .header {
    flex-direction: column;
    max-width: 96px;
    border-radius: 1.25rem;
    margin: 32px;
    padding: 2.0625rem 1.75rem;
  }
}
@media (min-width: 40em) {
  .header__logo {
    width: 32px;
    height: 25px;
  }
}
@media (min-width: 64em) {
  .header__nav {
    margin: 4.6875rem 0;
    flex: 1;
  }
}
.header__nav-link path {
  transition: fill 0.15s ease-in-out;
}
.header__nav-link:hover path {
  fill: white;
}
.header__nav-link--active {
  fill: white;
}
.header__nav ul {
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .header__nav ul {
    gap: 2rem;
  }
}
@media (min-width: 64em) {
  .header__nav ul {
    flex-direction: column;
    gap: 2.5rem;
  }
}
.header__profile {
  border: 1px solid white;
  border-radius: 50%;
}
@media (min-width: 40em) {
  .header__profile {
    width: 32px;
    height: 32px;
  }
}
@media (min-width: 64em) {
  .header__profile {
    width: 40px;
    height: 40px;
  }
}